import React from "react"
import { Section, Card, GifPlayer, Heading, Button, Layout } from "components"
import { Trans } from "@lingui/macro"
import { Link, useLocation, useHistory } from "react-router-dom"
import { country } from "App"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { fetchAssessmentResults } from "store/actions/contentfulActions"
import { Row, Col, Typography, Icon } from "antd"
import { baseStyles } from "styles/base"
import { ButtonLink } from "components"
import styled from "styled-components"
import { Tag } from "components"

const ResultCircle = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	width: 180px;
	height: 180px;
	border-radius: 50%;
	background-color: ${({ bg }) => bg || baseStyles.greenColor.primary};
	margin-bottom: 3em;
	.ant-typography {
		color: #fff;
		h2 {
			font-size: 4em;
		}
	}
`

export default function Success() {
	const { state = {} } = useLocation()
	const { push } = useHistory()
	const dispatch = useDispatch()
	const result = useSelector(({ contentful }) => contentful.singleResult)
	const assessmentResults = useSelector(({ contentful }) => contentful.assessmentResults)
	const { isSuccess, answers = [] } = state
	const points = answers.reduce((acc, curr) => acc + curr.point, 0)

	useEffect(() => {
		if (!isSuccess || Object.keys(result).length === 0) push(`/${country}/404`)
		dispatch(fetchAssessmentResults(points))
		return () => {
			localStorage.removeItem("quiz_taken")
			localStorage.removeItem("quiz_answers")
		}
	}, [dispatch, result])

	console.log({ result, assessmentResults })

	return (
		<Layout>
			<Section centered textAlign="center" maxWidth="1200px">
				<Card autoHeight>
					<Row>
						<Col lg={12}>
							<GifPlayer src="https://assets9.lottiefiles.com/temp/lf20_4Jjg2l.json" width={250} height={250} class="mb2em" />
							<Heading
								bold
								level={4}
								content={<Trans>Great job! :)</Trans>}
								subheader={<Trans>You've finished the test. And here's the result.</Trans>}
								marginBottom="3em"
							/>
						</Col>
						<Col lg={12}>
							<Section bg={baseStyles.lightGrey.two} className="p2em br-10px">
								<Row gutter={32}>
									<Col lg={10}>
										<ResultCircle bg={result.color || ""} className="m-auto__mobile mb2em__mobile">
											<Heading content={points} subheader={result.resultLabel} />
										</ResultCircle>
									</Col>
									<Col lg={14}>
										<Heading content={<Trans>Indicator</Trans>} level={4} />
										{assessmentResults.map((item) => (
											<div key={item.title} className="mb1em">
												<Tag color={item.color}>{item.resultLabel}</Tag>&nbsp;{item.from} - {item.to}
											</div>
										))}
									</Col>
								</Row>
								<Typography.Paragraph classNa="mb3em">{result.explanation}</Typography.Paragraph>
								<ButtonLink noHover className="px0">
									<span role="img" aria-label="doctor">
										üë®üèª‚Äç‚öïÔ∏è
									</span>
									&nbsp; <Trans>Talk to our specialist today</Trans>&nbsp;
									<Icon type="right" />{" "}
								</ButtonLink>
							</Section>
						</Col>
					</Row>
				</Card>
				<Section textAlign="center" centered paddingHorizontal="normal">
					<Link to={`/${country}`}>
						<Button type="ghost" icon="home">
							&nbsp;<Trans>Back to Home</Trans>
						</Button>
					</Link>
				</Section>
			</Section>
		</Layout>
	)
}
